.img-container {
  background: #009400;
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  min-width: 100%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

img{
  height:150px;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; 
}
.img-container:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1); 
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

